.homepage {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 1% 3%;
  }
  
  .overview {
    max-width: 80%;
    font-size: 16px;
    text-align: justify;
  }
  
  .card {
    display: flex;
    flex: 1;
    flex-wrap: wrap;
    direction: column;
    .subtitle {
      font-size: 16px;
      margin: 0% 5%;
      text-align: justify;
      // margin-top: 0; 
    }
  }


  .directory-menu {
    width: 100%;
    display: flex;
    // flex-wrap: nowrap;
    // justify-content: space-between;
  }
  
  .menu-item {
    min-width: 30%;
    height: 240px;
    flex: 1 1 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid black;
    margin:  2% 4%;
    background-position: center;
    background-size: cover;
    &:first-child {
      margin-right: 2%;
    }
  
    &:last-child {
      margin-left: 2%;
    }
  
    .content {
      height: 90px;
      padding: 0 25px;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      border: 1px solid black;
  
      .title {
        font-weight: bold;
        margin-bottom: 6px;
        font-size: 22px;
        color: white;
      }

    }
    
  }



  @media screen and (max-width: 800px){
    /*Big smartphones [426px -> 600px]*/
    .directory-menu {
      flex-wrap: wrap;
      flex-direction: column;
    }
    .menu-item {
      margin-top: 4%;
    }
  }
 